<template>
  <div style="max-width: 100%;box-sizing: border-box;" class="deadDtails">
    <myHeader
      :isBack="true"
      backPath="/deadApplicationRecord"
      title="死亡申报详情">
    </myHeader>
    <div class="mt4 list-block">
      <mt-cell title="批次号：" :value="batchNum"></mt-cell>
      <mt-cell title="死亡日期：" :value="deadDate"></mt-cell>
      <mt-cell title="死亡日龄：" :value="deadDay+' 天'"></mt-cell>
      <mt-cell title="申报死亡头数：" :value="deedNumber+' 头'"></mt-cell>
      <mt-cell title="死亡均重：" :value="deepWeight+' Kg'"></mt-cell>
      <mt-cell title="实际死亡头数：" :value="actualDeedNumber+' 头'" v-if="billStatusIndex==30"></mt-cell>
      <div class="longitude mint-cell">
        <div class="width50 left">
          <span>经度：</span>
          <span>{{longitude}}</span>
        </div>
        <div class="width50 left">
          <span>纬度：</span>
          <span>{{dimension}}</span>
        </div>
      </div>
      <div class="photo-info">
        <div class="devied"></div>
        <mt-cell title="死亡猪只拍照："></mt-cell>
        <div class="photo-body">
          <div class="photoList" v-if="!imgList.length">
            <img src="../../../../assets/photo-placeholder.png">
            <span class="delete-photo"></span>
          </div>
          <div class="photoList" v-else v-for="(item,index) in imgList" :key="index">
            <img :src="item.img">
          </div>
        </div>
      </div>
    </div>
    
    <Signature v-show="isShowSignature" ref="signature"></Signature>
    
    <SubmitBtn v-if="recordState != 2 && billStatusIndex==30"
               @click.native="submitData()">
    </SubmitBtn>
  
  </div>
</template>
<script>
  import myHeader from "@/components/Header";
  import Signature from '@/components/Signature'
  import SubmitBtn from '@/components/SubmitBtn'
  import {getDeathRecordDetails, saveDeathApplySinger} from '@/api/business'
  import {Indicator} from 'mint-ui'
  import {getParam} from '@/utils/wxUtil'
  
  export default {
    
    name: "DeadApplicationRecordDetail",
    components: {
      myHeader,
      SubmitBtn,
      Signature
    },
    data() {
      return {
        batchNum: "",
        deadDate: "",
        deadDay: '',
        deedNumber: "",
        deepWeight: "",
        actualDeedNumber: '',
        longitude: '',
        dimension: '',
        imgList: [],
        rcordId: '',
        recordState: '',
        billStatusIndex: '',
        signerImgs: '',
        isShowSignature: true
      };
    },
    created() {
      this._getDeathRecordDetails();
      // this.rcordId = this.$route.params.rcordId;
      this.rcordId = getParam('rcordId').replace(/%3D/g, '=').trim();
    },
    mounted() {
    
    },
    watch: {
      recordState(newName, oldName) {
        // 已完成签名，只需要展示签名图片，不能编辑
        if (this.recordState == 2 && this.billStatusIndex == 30) {
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.fromDataURL(this.signerImgs);
            this.$refs.signature.disabled = true;
          });
        }
        
        if (this.billStatusIndex == 30 && this.recordState != 2) { // 已经审核，可以签字，可以编辑
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.disabled = false;
          });
        }
        
        if (this.billStatusIndex != 30) {  // 没有审核或者已经取消
          this.isShowSignature = false;
        }
        
      }
    },
    methods: {
      _getDeathRecordDetails() {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        // const rcordId = this.$route.params.rcordId;
        const rcordId = getParam('rcordId').replace(/%3D/g, '=').trim();
        
        getDeathRecordDetails(rcordId).then(res => {
          Indicator.close();
          
          let data = res.data;
          data.billStatusIndex ? this.billStatusIndex = data.billStatusIndex : null;
          data.state ? this.recordState = data.state : null;
          data.signerUrl.length ? this.signerImgs = data.signerUrl[0].img : null;
          
          this.batchNum = data.batchNo;
          this.deadDate = data.dieDate;
          this.deadDay = data.dieDay;
          this.deedNumber = data.applyDieCnt;
          this.deepWeight = data.dieAvg;
          this.actualDeedNumber = data.dieCnt;
          this.longitude = data.longitude;
          this.dimension = data.latitude;
          this.imgList = data.imgUrl;
          
        }, err => {
          Indicator.close();
        });
      },
      submitData() {
        let signerImg = this.$refs.signature.save();
        
        const data = {
          rcordId: this.rcordId,
          fileEntry: {
            imgContent: signerImg,
            imgType: 'png'
          }
        };
        
        saveDeathApplySinger(data).then(res => {
          this.$toast({
            message: '签字确认成功！',
            duration: 1000
          });
          
          this.$router.push('/deadApplicationRecord');
        });
        
      }
      
    }
  };
</script>
<style lang="scss" scoped>
  .deadDtails {
    min-height: 100vh;
    background-color: #f8f8f8;
    padding: 0.12rem 0;
    
    .list-block {
      font-size: 0.16rem;
      background-color: #fff;
      
      .deepWeight {
        display: block;
        margin-left: 0.06rem;
      }
      
      .img {
        width: 0.1rem;
        height: 0.06rem;
        margin-left: 0.08rem;
        background: url("~@/assets/icon-next.png") no-repeat;
        background-size: 100%;
      }
    }
    
    .longitude {
      display: flex;
      overflow: hidden;
      position: relative;
      line-height: 1;
      padding: 0 0.1rem;
      
      .width50 {
        width: 50%;
        
        span {
          display: flex;
          float: left;
          flex: 1;
          color: #000;
          min-height: 0.44rem;
          align-items: center;
          word-break: break-all;
          justify-content: flex-start;
          vertical-align: middle;
        }
      }
      
      .left {
        float: left;
      }
    }
    
    .photo-info {
      .devied {
        height: 0.1rem;
        background: #f8f8f8;
      }
      
      background: #fff;
      
      .mint-cell {
        border-bottom: none;
      }
      
      .photo-body {
        margin-top: 0.25rem;
        padding: 0 0.1rem 0.25rem;
        
        .photoList,
        .tabke-photo {
          width: 0.9rem;
          height: 0.9rem;
          display: inline-block;
          /*border: 1px dashed #868282;*/
          margin-left: 0.1rem;
          
          img {
            height: .88rem;
            width: .88rem;
          }
        }
      }
    }
  }
</style>



